@font-face {
  font-family: 'icomoon';
  src: url('../assets/fonts/icomoon.ttf?8tkig7') format('truetype'),
    url('../assets/fonts/icomoon.woff?8tkig7') format('woff'),
    url('../assets/fonts/icomoon.svg?8tkig7#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

